.tile {
    display: block;
    /* width: 40px;
    height: 40px; */
    text-transform: uppercase;
    cursor: pointer;
    color: black;
    background-color: white;
    flex: 1;
    padding: 8px;
}

.tile.challenger {
    background-color: #ed8c34;
}
.tile.opponent {
    background-color: #59c5e4;
}

.tile.selected {
    color: white;
    background-color: black;
}